.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: black;
}

.conversation-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 700px;
  height: 500px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow-y: auto;
  background-color: #bfcbd9;
}

.message {
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  position: relative;
}

.user {
  background-color: #8ac4ff;
  align-self: flex-end;
  color: #000;
  border: 1px solid #000;
}

.bot {
  background-color: #004ba0;
  align-self: flex-start;
  color: #fff;
  border: 1px solid #000;
  position: relative;
}

.copy-button {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 5px;
  background-color: #333;
  color: #fff;
  font-family: Arial, sans-serif;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.bot-button {
  padding: 5px 10px;
  background-color: #004ba0;
  color: #fff;
  font-family: Arial, sans-serif;
  border: 1px solid #fff;
  border-radius: 5px;
}

.input-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  width: 700px;
}

.input-container {
  display: flex;
  align-items: center;
  width: 100%;
}

.input-container input {
  flex: 1;
  padding: 5px;
  margin-bottom: 5px;
  margin-right: 10px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  background-color: #8ac4ff;
  color: #000;
}

.input-container button {
  padding: 5px 10px;
  background-color: #333;
  color: #fff;
  font-family: Arial, sans-serif;
}

.spinner {
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-left-color: #888;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.logo {
  width: 200px;
  height: auto;
}